<template>
  <div class="wrapper" ref="wrapper">
	<div class="recommend">
		<slideshow :slides="slides">      
        </slideshow>
        <div class="circle-bar">
            <div class="circle circle-1">
            <div class="img">
              </div>
               <p>私人FM</p>
            </div>
            <div class="circle circle-2">
            <div class="img">5</div>
               <p>每日歌曲推荐</p>
            </div>
            <div class="circle circle-3">
               <div class="img">
               </div>
               <p>云音乐热歌榜</p>
            </div>
        </div>
        <div class="h2">推荐歌单</div>
        <div class="songlist">
        <ul>
           <li class="song" v-for="item in songlists">
               <img :src="item.picUrl" @click="$router.push({name:'songlistsdetail',query:{id:item.id,picUrl:item.picUrl,name:item.name}})">
               <div class="content">{{item.name}}</div>
           </li>
        </ul>
        </div>
        <div class="h2">推荐mv</div>
        <div class="mvlist">
        <ul>            
           <li class="mv" v-for="item in resultmv">
               <img :src="item.picUrl">
               <div class="content">
               {{item.name}}
               <div class="autor">{{item.artistName}}</div>
               </div>
           </li>
        </ul>
        </div>
	   </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
import slideshow from "../slideshow/slideshow"
	export default{
       name:'songlists',
       components:{
       	slideshow
       },
       mounted () {
       /* this.$http.get('../../../static/data.json')*/
        this.$http.get('http://localhost:3000/personalized')
        .then(res =>{
            res.data.result.forEach(item =>{
                let obj = {
                    name:item.name,
                    id:item.id,
                    picUrl:item.picUrl,
                };
                this.songlists.push(obj);
            })
            return this.$http.get('http://localhost:3000/personalized/mv')
           .then(res => {
            this.resultmv=res.data.result;
            this.$nextTick(() => {
            this._initScroll();
          });
           })
        });
       },
       methods:{
            add:function (item) {
            this.$refs.musicmenu.show();
            console.log()
            },
            show: function (item) {
                this.$refs.musicmenu.show(item);
            },
           _initScroll() {
           if (!this.wrapperScroll) {
            this.wrapperScroll = new BScroll(this.$refs.wrapper, {
            click: true
          });
        } else {
          this.wrapperScroll.refresh();
        }
    }
  },
     data (){
       return{
        result:[],
        resultmv:[],
        scrollY:0,
        songlists:[],
        slides:[
        {
             src:require("../slideshow/1.png"),
             title:'1',
        },     
        {
             src:require("../slideshow/2.png"),
             title:'2',
        },
        {
             src:require("../slideshow/3.png"),
             title:'3',
        },
        {
             src:require("../slideshow/4.png"),
             title:'4',
        },
         {
             src:require("../slideshow/5.png"),
             title:'5',
        },
        {
             src:require("../slideshow/6.png"),
             title:'6',
        },
        {
             src:require("../slideshow/7.png"),
             title:'7',
        },
        {
             src:require("../slideshow/8.png"),
             title:'8',
        }
       ]
              }
             }        
	}
</script>
<style lang="less" scoped>
	.wrapper{
        position:absolute;
        top:90px;
        bottom:60px;
        width:100%;
        overflow:hidden;
        background-color:#fff;
        .circle-bar{
            display: flex;
            padding-bottom:20px;
            border-bottom: 1px solid rgba(102, 102, 102, 0.24);
            .circle{
                flex:1;
                text-align: center;
                &.circle-1{
                .img{
                    margin:0 auto;
                    border-radius:50%;
                    border:1px solid #d43c33;
                    background: url(cricle-1.png) no-repeat 5px;
                    background-size: 40px 40px;
                    width:50px;
                    margin-bottom: 10px;
                    height:50px;
                }
            }
                &.circle-2{
                .img{
                    margin:0 auto;
                    border-radius:50%;
                    border:1px solid #d43c33;
                    width:50px;
                    margin-bottom: 10px;
                    height:50px;
                    line-height:50px;
                    font-size: 24px;
                    color:#d43c33;
                }
            }
                &.circle-3{
                .img{
                    margin:0 auto;
                    border-radius:50%;
                    border:1px solid #d43c33;
                    background: url(cricle-2.png) no-repeat 5px;
                    background-size: 40px 40px;
                    width:50px;
                    height:50px;
                    margin-bottom:10px;
                }
            }
            }
        }
        .h2{    
            margin-top: 20px;
            color:#000;
            padding-left:10px;
            border-left: 3px solid #d43c33;
            font-size:16px;
            margin-bottom: 10px;
             }
        .songlist{
            margin-top: 20px;
            color:#000;
             .song{
                min-height: 154px;
                width:33%;
                margin-right:1px;
                display:inline-block;
                img{
                width:100%
                }
                .content{
                    height: 38px;
                    word-wrap: break-word;
                    overflow: hidden;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    font-size: 12px;
                    color: #333;
                    line-height:1.5;
                    letter-spacing: 1px;
                }
             }
        }
        .mvlist{
            margin-top:20px;
            color:#000;
            .mv{
                width:49%;
                margin-left: 1px;
                margin-right:1px;
                display:inline-block;
                img{
                    width:100%;
                }
                .content{
                    height:38px;
                    line-height:20px;
                    text-overflow:ellipsis;
                    white-space:nowrap;
                    overflow:hidden;
                .autor{
                    color:#666;
                 }
              }
            }
        }
    }
</style>	


