<template>
  <div id="app">
       <router-view></router-view>  
       <div class="foot-tab" v-if="show_foot">
         <div class="tab-item">
           <router-link :to="{path:'/findmusic'}" active-class="act"> 
           <icon name="wangyi" scale="4"></icon> 
           <p>发现音乐</p>
           </router-link>
         </div>
         <div class="tab-item">
           <router-link :to="{path:'/mymusic'}" active-class="act">  
           <icon name="music" scale="4"></icon><p>我的音乐</p>
           </router-link>
           </div>
         <div class="tab-item">
           <router-link :to="{path:'/friend'}" active-class="act"> 
           <icon name="friend" scale="4"></icon>
           <p>朋友</p>
           </router-link>
         </div>
         <div class="tab-item">
          <router-link :to="{path:'/user'}" active-class="act">  
          <icon name="user" scale="4"></icon> 
          <p>账号</p>
          </router-link>
         </div>
       </div>
    </div>
</template>

<script>
  export default {
    data() {
        return {
          show_foot: true
      }
    },
    mounted(){
        var self = this;
        if(self.$route.path=='/songlistsdetail'){
           self.show_foot=false;
        }
        if(self.$route.path=='/playsong'){
          self.show_foot=false;
        }
        if(self.$route.path=='/search'){
          self.show_foot=false;
        }
    }
  }
</script>

<style lang="less">
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  list-style-position: outside;
  list-style:none;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
body {
  font-family: "Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3","Microsoft YaHei UI","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
  font-size: 14px;
  color: #666;
}
   .foot-tab{
    position:fixed;
    padding:3px 8px;
    bottom:0;
    left:0;
    height:60px;
    background:rgb(7,17,27);
    width:100%;
    box-sizing: border-box;
    display:flex;
    text-align: center;
    .tab-item{
          flex:1;
          text-align: center;
          a{
            display:block;
            font-size: 12px;
            }
          .act{
             color: #fff;
            }
        }
   }
</style>



